@use '../../../Public/SCSS/Utilities/Mixins' as *;
@use '../../../Public/SCSS/Utilities/Variables' as *;

.blue-and-white {
  border-left: 0.3rem groove $secondaryColorDark;
  border-right: 0.3rem groove $secondaryColorDark;
  background-color: $secondaryColorDark;
  .appoint-me-container {
    background-color: $secondaryColor;
    &__sub-container {
      background-color: $colorOffWhite;
      &__heading {
        border-bottom: 0.3rem groove $secondaryColorDark;
        .select-date-button {
          &:hover {
            background-color: $secondaryColor;
            color: $colorOffWhite;
          }
        }
      }
      &__calendar {
        .hour {
          &:nth-of-type(2n) {
            background-color: rgba($secondaryColorDark, 0.25);
          }
          &:nth-of-type(2n + 1) {
            .hour__time {
              background-color: rgba($colorOffWhite, 0.5);
            }
          }
          .one-hour {
            @include SizeAndPositioning(relative, 100%, 100%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
          }
          .thirty-minutes {
            @include SizeAndPositioning(relative, 50%, 100%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
            &:not(:last-of-type) {
              border-bottom: 0.2rem dashed rgba($secondaryColorLighter, 0.25);
            }
          }
          .fifteen-minutes {
            @include SizeAndPositioning(relative, 25%, 100%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
            &:not(:last-of-type) {
              border-bottom: 0.2rem dashed rgba($secondaryColorLighter, 0.25);
            }
          }
          &:hover {
            background-color: $secondaryColor;
            .hour__part {
              border-color: $secondaryColor;
            }
            .hour__time {
              background-color: transparent;
            }
          }
        }
        .blacked-out {
          background-color: $colorDarkGrey;
          .hour__time {
            background-color: transparent;
          }

          &:nth-of-type(2n) {
            background-color: $colorGrey;
          }

          &:nth-of-type(2n + 1) {
            background-color: $colorDarkGrey;
            .hour__time {
              background-color: transparent;
            }
          }
          .hour__part.fifteen-minutes,
          .hour__part.thirty-minutes,
          .hour__part.one-hour {
            border-top-color: $colorLighterGrey;
            border-bottom-color: $colorLighterGrey;
            border: none;
          }

          &:hover {
            cursor: default;
            background-color: $colorDarkGrey;
            .hour__time {
              background-color: transparent;
            }
            .hour__part.fifteen-minutes,
            .hour__part.thirty-minutes,
            .hour__part.one-hour {
              border-top-color: $colorLighterGrey;
              border-bottom-color: $colorLighterGrey;
              border: none;
            }
          }
        }
      }
      .modal {
        &--select-date,
        &--select-time {
          background-color: rgba($secondaryColor, 0.975);
          &__date-picker-container {
            input,
            select {
              border-bottom: 0.3rem groove rgba($colorOffWhite, 0.8);
            }

            select {
              option {
                background-color: $secondaryColorDark;
              }
            }
          }
        }

        &--select-date {
          .button {
            &--modal {
              border: 0.2rem solid rgba($colorOffWhite, 0.8);
              background-color: rgba($colorOffWhite, 0.25);
              &:hover {
                border-color: $colorOffWhite;
                background-color: $colorOffWhite;
              }
            }
          }
        }
        &--select-time {
          .form {
            &__input {
              border-bottom: 0.3rem groove rgba($colorOffWhite, 0.8);
              &:focus {
                border-bottom-color: $colorOffWhite;
              }
              &--hour,
              &--minute,
              &--email,
              &--phone {
                @extend .form__input;
              }
            }

            &__select {
              border-bottom: 0.3rem groove rgba($colorOffWhite, 0.8);
              &:focus {
                border-bottom-color: $colorOffWhite;
              }
              &--hour,
              &--minute {
                @extend .form__select;
              }
            }

            &__section {
              &--commPreference {
                label {
                  border: 0.2rem groove rgba($colorOffWhite, 0.8);
                  transition: color 0.5s, background-color 0.5s;
                  &:first-of-type {
                    border-right-width: 0.1rem;
                  }
                  &:last-of-type {
                    border-left-width: 0.1rem;
                  }

                  &.clicked {
                    border-color: $colorOffWhite;
                    background-color: $colorOffWhite;
                  }
                }
              }
            }
            &--appointment {
              .button {
                &--modal {
                  border: 0.2rem solid rgba($colorOffWhite, 0.8);
                  background-color: rgba($colorOffWhite, 0.25);
                  &:hover {
                    border-color: $colorOffWhite;
                    background-color: $colorOffWhite;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
